import { ROWS } from './ludo.constant';

export default {
  onDiceChange(value) {
    this.dice = value;
    this.disabled = true;
    this.setAvailable(false);

    if (this.dice == 6) {
      this.setAvailable(true, this.activePlayer);
    } else {
      const { hasAvailable, availableCount, availableAt } = this.setAvailable(
        true,
        this.activePlayer,
        true
      );
      if (hasAvailable) {
        if (availableCount == 1) {
          this.onDiceChange1(availableAt);
        }
      } else {
        this.onDiceChangeCallback();
      }
    }
  },
  onDiceChange1(index) {
    this.setAvailable(false);

    const player = this.players[this.activePlayer];
    const item = player.items[index];
    // player.isActive = true;

    if (!item.value) {
      if (this.dice == 6) {
        item.value = 1;
      }

      this.onDiceChangeCallback();
      return;
    } else if (item.value + this.dice > 57) {
      this.onDiceChangeCallback();
      return;
    }

    this.reduceDramatically(
      this.dice,
      250,
      () => {
        item.value++;
        this.key++;
      },
      () => {
        this.players.forEach((_player, index) => {
          if (index != this.activePlayer) {
            _player.items.forEach((_item) => {
              if (
                !this.safe.includes(_item.value) &&
                _item.value == item.value + this.diff[player.id][_player.id]
              ) {
                // this.reduceDramatically(_item.value, 10, () => {
                // _item.value--;
                // if (_item.value == 0) {
                _item.value = null;
                // }
                this.key++;
                // });
              }
            });
          }
        });

        this.onDiceChangeCallback();
      }
    );
  },
  onDiceChangeCallback() {
    this.disabled = false;

    if (this.levelCompleted()) {
      console.log('completed!');
    } else {
      if (this.dice != 6) {
        if (this.activePlayer == 1) {
          this.activePlayer = this.isCompletedNested(3, 2, 0);
        } else if (this.activePlayer == 2) {
          this.activePlayer = this.isCompletedNested(0, 1, 3);
        } else if (this.activePlayer == 3) {
          this.activePlayer = this.isCompletedNested(2, 0, 1);
        } else {
          this.activePlayer = this.isCompletedNested(1, 3, 2);
        }
      }
    }

    // setTimeout(() => {
    //   this.$refs.dice.onClick();
    // }, 1000);
  },
  reduceDramatically(count, speed, iterator, cb) {
    // if (!speed) return cb();

    const interval = setInterval(function() {
      if (count == 0) {
        clearInterval(interval);
        if (cb) cb();
      } else {
        count--;
        iterator();
      }
    }, speed);
  },
  levelCompleted() {
    return (
      this.players.filter((_, index) => this.isCompleted(index)).length == 3
    );
  },
  isCompletedNested(a, b, c) {
    return this.isCompleted(a) ? (this.isCompleted(b) ? c : b) : a;
  },
  isCompleted(index) {
    return !this.players[index].items.some((x) => x.value != 57);
  },
  setAvailable(boolean, index, value) {
    let hasAvailable = false;
    let availableCount = 0;
    let availableAt = 0;

    const closure = (player) => {
      player.items.forEach((item, _index) => {
        if (!value || (item.value && item.value < 57)) {
          item.available = boolean;
          hasAvailable = true;
          availableAt = _index;
          availableCount++;
        }
      });
    };

    if (index || index == 0) {
      const player = this.players[index];
      closure(player);
    } else {
      this.players.forEach(closure);
    }

    return {
      hasAvailable,
      availableCount,
      availableAt,
    };
  },
  setRows() {
    this.rows = ROWS;

    this.history = [];
    this.activeBall = 1;
    this.activeColumn = null;

    this.copyRows = _.cloneDeep(this.rows);
  },
};
